@charset "UTF-8";
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1;
  font-size: 16px;
  width: 100%;
  margin: 0px; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

a {
  text-decoration: none; }

input[type="text"],
input[type="email"],
input[type="tel"],
input[type="number"],
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  appearance: none;
  outline: 0;
  box-shadow: none; }

*::-webkit-input-placeholder {
  color: #7a6862;
  opacity: 1;
  font-size: 16px; }

*:-moz-placeholder {
  color: #7a6862;
  opacity: 1;
  font-size: 16px; }

*::-moz-placeholder {
  color: #7a6862;
  opacity: 1;
  font-size: 16px; }

*:-ms-input-placeholder {
  color: #7a6862;
  opacity: 1;
  font-size: 20px; }

body input:focus:required:invalid,
body textarea:focus:required:invalid {
  border: 1px solid #6a4c43; }

body input:required:valid,
body textarea:required:valid {
  border: 1px solid green; }

input::-webkit-inner-spin-button,
input::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0px; }

@font-face {
  font-family: 'Conv_SegoeWP';
  src: url("../fonts/SegoeWP.eot");
  src: local("☺"), url("../fonts/SegoeWP.woff") format("woff"), url("../fonts/SegoeWP.ttf") format("truetype"), url("../fonts/SegoeWP.svg") format("svg");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: 'Conv_SegoeWP-Light';
  src: url("../fonts/SegoeWP-Light.eot");
  src: local("☺"), url("../fonts/SegoeWP-Light.woff") format("woff"), url("../fonts/SegoeWP-Light.ttf") format("truetype"), url("../fonts/SegoeWP-Light.svg") format("svg");
  font-weight: normal;
  font-style: normal; }

.header .main-menu {
  padding: 15px 0px 15px 0px;
  display: flex;
  justify-content: space-between; }
  .header .main-menu .logo a {
    display: flex;
    align-items: center; }
    .header .main-menu .logo a img {
      max-width: 100%;
      height: auto; }
    .header .main-menu .logo a p {
      font-size: 42px;
      font-family: 'Conv_SegoeWP-Light';
      color: #000;
      margin-left: 5px; }
  .header .main-menu .nav {
    display: flex;
    align-items: flex-end; }
    .header .main-menu .nav ul li {
      display: inline-block;
      padding-left: 30px; }
      .header .main-menu .nav ul li a {
        font-size: 20px;
        color: #000;
        font-family: 'Conv_SegoeWP';
        padding-bottom: 9px; }
      .header .main-menu .nav ul li a:hover {
        border-bottom: 2px solid #5094f2; }

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent; }

.slick-slide img {
  display: block;
  min-width: 100%;
  height: auto; }

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0; }
  .slick-list:focus {
    outline: none; }
  .slick-list.dragging {
    cursor: pointer;
    cursor: hand; }

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0); }

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto; }
  .slick-track:before, .slick-track:after {
    content: "";
    display: table; }
  .slick-track:after {
    clear: both; }
  .slick-loading .slick-track {
    visibility: hidden; }

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none; }
  [dir="rtl"] .slick-slide {
    float: right; }
  .slick-slide img {
    display: block; }
  .slick-slide.slick-loading img {
    display: none; }
  .slick-slide.dragging img {
    pointer-events: none; }
  .slick-initialized .slick-slide {
    display: block; }
  .slick-loading .slick-slide {
    visibility: hidden; }
  .slick-vertical .slick-slide {
    display: block;
    height: auto;
    border: 1px solid transparent; }

.slick-arrow.slick-hidden {
  display: none; }

.container {
  width: 100%;
  max-width: 975px;
  margin: 0 auto;
  padding: 0 5px;
  box-sizing: border-box;
  background: none; }
